import React from 'react'
import './Admin.css'
import logo from '../assets/OIP-removebg-preview.png'
import AdminHome from './AdminHome'
import AdminEmployeer from './AdminEmployeer'
import AdminJobseeker from './AdminJobseeker'
import { useState } from 'react'



export default function Admin() {
    
   

    const[adminhome,setAdminhome] = useState(true);
    const showAdminhome = () => {
        if (adminhome == false) {
            setAdminhome(true)
            setAdminjs(false)
            setAdmine(false)
        } else {
            setAdminhome(true)
        }
    }

    const[adminjs,setAdminjs] = useState(false);
    const showAdminJs = () => {
        if (adminjs == false) {
            setAdminjs(true)
            setAdminhome(false)
            setAdmine(false)
        } else {
            setAdminhome(false)
        }
    }

    const[admine,setAdmine] = useState(false);
    const showAdminE = () => {
        if (admine == false) {
            setAdmine(true)
            setAdminhome(false)
            setAdminjs(false)
        } else {
            setAdmine(false)
        }
    }
    
  return (
    <>
     <div className="admin-home">
        <div className="admin-sidebar">
            <div className="admin-photo">
           <img src={logo} alt="" />
            </div>
            <div className="admin-navigation">
                <h2 onClick={showAdminhome}>Home</h2>
                <h2 onClick={showAdminJs}>JobSeeker</h2>
                <h2 onClick={showAdminE}>Employeer</h2>
            </div>
        </div>
        <div className="admin-main">
            <h1>Admin</h1>
            <div className="admin-content">
               {
                  adminhome && <AdminHome></AdminHome>
               }
               {
                  adminjs && <AdminJobseeker></AdminJobseeker>
               }
               {
                  admine && <AdminEmployeer></AdminEmployeer>
               }
            </div>
        </div>
     </div>
</>
  )
}
