import React from 'react'
import './Admin.css'
import { ref, get } from 'firebase/database'
import { useEffect, useState } from 'react'
import { database } from './Firebase-config'

export default function AdminJobseeker() {

    const [users, setUsers] = useState([])

    useEffect(() => {
        const usersRef = ref(database, 'users');
        get(usersRef).then((snapshot) => {
            if (snapshot.exists()) {
                const usersArray = Object.entries(snapshot.val()).map(([id, data]) => ({
                    id,
                    ...data,
                }));
                setUsers(usersArray);
            } else {
                console.log("No data Avilabel")
            }
        }).catch((error) => {
            console.error(error);
        });
    });



    return (
        <div className="jobSeeker">
            <>
                <a
                    className="btn btn-success"
                    style={{ float: "left", marginRight: 20 }}
                    href="https://codepen.io/collection/XKgNLN/"
                    target="_blank"
                >
                    JobSeeker Users Data
                </a>
                <table id="example" className="table" cellSpacing={0} width="100%">
                    <thead>
                        <tr>
                            <th>Name</th>
                            <th>Address</th>
                            <th>Email</th>
                            <th>City</th>
                            <th>Zip Code</th>
                            <th>Strength</th>
                            <th>Weakness</th>
                            <th>Experience</th>
                            <th>Telephone</th>
                            <th>Start Date</th>
                        </tr>
                    </thead>
                    <tbody>

                        {
                            users.map((data) => {
                                return (
                                    <>
                                        <tr>
                                            <td>{data.fullName}</td>
                                            <td>{data.Address}</td>
                                            <td>{data.email}</td>
                                            <td>{data.City}</td>
                                            <td>{data.Zip}</td>
                                            <td>{data.Strength}</td>
                                            <td>{data.Weakness}</td>
                                            <td>{data.Experience}</td>
                                            <td>{data.telephone}</td>
                                            <td>{data.startDate}</td>
                                        </tr>
                                    </>
                                )
                            })
                        }

                    </tbody>
                </table>
            </>
        </div>
    )
}
