import React from 'react'
import './EMP.css'
import { Link } from 'react-router-dom'

export default function EmployeeForm() {
    return (
        <>
        <div className="wrapper">
            <div className="ef-image"></div>
            <div className="container">
                <div className="navbar-header">
                    <Link className="navbar-brand" to="/">
                        Home-JobSeeker
                    </Link>
                </div>
            </div>
            <div className="col-md-6 offset-md-3 mt-5">
                <h1>Submit Your Details</h1>
                <p>What other people thought about the service provided by BT Consultancy</p>
                <form>
                    <div className="form-group">
                        <label htmlFor="exampleInputName">Company Name</label>
                        <input
                            type="text"
                            name="fullname"
                            className="form-control"
                            id="exampleInputName"
                            placeholder="Enter your name and surname"
                            required="required"
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="exampleInputEmail1" required="required">
                            Email address
                        </label>
                        <input
                            type="email"
                            name="email"
                            className="form-control"
                            id="exampleInputEmail1"
                            aria-describedby="emailHelp"
                            placeholder="Enter your email address"
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="inputAddress">Address</label>
                        <input
                            type="text"
                            name="address"
                            className="form-control"
                            id="inputAddress"
                            placeholder="1234 Main St"
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="inputAddress">Mobile Number</label>
                        <input
                            type="tel"
                            name="MobileNumber"
                            className="form-control"
                            placeholder="Enter your Mobile Number"
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="inputAddress">Contact Person</label>
                        <input
                            type="text"
                            name="ContactPerson"
                            className="form-control"
                            placeholder="Enter here"
                        />
                    </div>
                    <div className="form-group">
                        <label htmlFor="inputAddress">Destination</label>
                        <textarea
                            type="text"
                            name="Destination"
                            className="form-control"
                            placeholder="Enter here"
                        />
                    </div>
                    <button type="submit" className="btn btn-primary">
                        Submit
                    </button>
                </form>
            </div>
            </div>
        </>
    )
}
