import React from 'react'
import './JSF.css'
import { Link } from 'react-router-dom'
import { useState } from 'react'

export default function JobSeekerForm() {

    const [userData, setUserData] = useState({
        fullName: "",
        email: "",
        Address: "",
        City: "",
        Zip: "",
        Strength: "",
        Weakness: "",
        Experience: "",
        Skills: "",
        telePhone: "",
        startDate: ""
    });

    let name, value;
    const postUserData = (event) => {
        name = event.target.name;
        value = event.target.value;
        setUserData({ ...userData, [name]: value });
    }

    const submitData = async (event) => {
        event.preventDefault();
        const { fullName,
            email,
            Address,
            City,
            Zip,
            Strength,
            Weakness,
            Experience,
            Skills,
            telePhone,
            startDate } = userData;
        if (fullName &&
            email &&
            Address &&
            City &&
            Zip &&
            Strength &&
            Weakness &&
            Experience &&
            Skills &&
            telePhone &&
            startDate) {
            const res = await fetch("https://jobportal-dd359-default-rtdb.asia-southeast1.firebasedatabase.app/users.json",
                {
                    method: "POST",
                    headers: {
                        "Content-Type": "application/json"
                    },
                    body: JSON.stringify({
                        fullName,
                        email,
                        Address,
                        City,
                        Zip,
                        Strength,
                        Weakness,
                        Experience,
                        Skills,
                        telePhone,
                        startDate
                    })
                }
            );
            if (res) {
                console.log(res)
                setUserData({
                    fullName: "",
                    email: "",
                    Address: "",
                    City: "",
                    Zip: "",
                    Strength: "",
                    Weakness: "",
                    Experience: "",
                    Skills: "",
                    telePhone: "",
                    startDate: ""
                })
                alert("Data Stored");
            } else {
                alert("Please Fill the Data");
            }
        } else {
            alert("Please Fill the Data");
        }
    }

    return (
        <>
        <div className="wrapper">
            <div className="container">
                <div className="jsf-image">
                    </div>
                <div className="navbar-header">
                    <Link className="navbar-brand" to="/">
                    Home-JobSeeker
                    </Link>
                </div>
            </div>
            <div className="col-md-6 offset-md-3 mt-5">
                <h1>Submit Your Details</h1>
                <form>
                    {/* Full Name  */}
                    <div className="form-group">
                        <label>Full Name</label>
                        <input
                            type="text"
                            name="fullName"
                            className="form-control"
                            id="exampleInputName"
                            placeholder="Enter your name and surname"
                            required="required"
                            value={userData.fullName}
                            onChange={postUserData}
                        />
                    </div>
                    {/* Email  */}
                    <div className="form-group">
                        <label  required="required">
                            Email address
                        </label>
                        <input
                            type="email"
                            name="email"
                            className="form-control"
                            placeholder="Enter your email address"
                            value={userData.email}
                            onChange={postUserData}
                        />
                    </div>
                    {/* Address  */}
                    <div className="form-group">
                        <label htmlFor="inputAddress">Address</label>
                        <input
                            type="text"
                            name="Address"
                            className="form-control"
                            id="inputAddress"
                            placeholder="1234 Main St"
                            value={userData.Address}
                            onChange={postUserData}
                        />
                    </div>
                    {/* City and Zip  */}
                    <div className="form-row">
                        <div className="form-group col-md-6">
                            <label htmlFor="inputCity">City</label>
                            <input
                                type="text"
                                name="City"
                                className="form-control"
                                placeholder="Istanbul"
                                value={userData.City}
                                onChange={postUserData}
                            />
                        </div>
                        <div className="form-group col-md-2">
                            <label htmlFor="inputZip">Zip</label>
                            <input
                                type="text"
                                name="Zip"
                                className="form-control"
                                placeholder={34000}
                                value={userData.Zip}
                                onChange={postUserData}
                            />
                        </div>
                    </div>
                    {/* Strength  */}
                    <div className="form-group">
                        <label>
                            Strength
                        </label>
                            <input
                                className="form-control"
                                name="Strength"
                                type="text"
                                placeholder='Enter here'
                                value={userData.Strength}
                                onChange={postUserData}
                            />
                    </div>
                    {/* Weakness  */}
                    <div className="form-group">
                        <label>
                            Weakness
                        </label>
                            <input
                                className="form-control"
                                name="Weakness"
                                type="text"
                                placeholder='Enter here'
                                value={userData.Weakness}
                                onChange={postUserData}
                            />
                    </div>
                    {/* Experience  */}
                    <div className="form-group">
                        <label>
                            Experience
                        </label>
                            <input
                                className="form-control"
                                name="Experience"
                                type="text"
                                placeholder='Enter here'
                                value={userData.Experience}
                                onChange={postUserData}
                            />
                    </div>
                    {/* Skills */}
                    <div className="form-group">
                        <label htmlFor="inputAddress">Skills</label>
                        <textarea
                            type="text"
                            name="Skills"
                            className="form-control"
                            placeholder="Enter Your Skills"
                            value={userData.Skills}
                            onChange={postUserData}
                        />
                    </div>
                    {/* Telephone  */}
                    <div className="form-group">
                        <label  className="col-1 col-form-label">
                            Telephone
                        </label>
                        <div className="col-10">
                            <input
                                className="form-control"
                                name="telePhone"
                                type="tel"
                                defaultValue="1-(555)-555-5555"
                                id="example-tel-input"
                                value={userData.telePhone}
                                onChange={postUserData}
                            />
                        </div>
                    </div>
                    {/* StartDate  */}
                    <div className="form-group">
                        <label  className="col-3 col-form-label">
                            Start Date
                        </label>
                        <div className="col-10">
                            <input
                                className="form-control"
                                name="startDate"
                                type="date"
                                defaultValue="2020-02-01"
                                id="example-date-input"
                                value={userData.startDate}
                                onChange={postUserData}
                            />
                        </div>
                    </div>
                    {/* Cv Pic  */}
                    <div className="form-group mt-3">
                        <label className="mr-4">Upload your CV:</label>
                        <input type="file" name="file" />
                    </div>
                    <button type="submit" className="btn btn-primary" onClick={submitData}>
                        Submit
                    </button>
                </form>
            </div>
            </div>
        </>
    )
}
